import React from 'react';

const Section6: React.FC = () => {
  return (
    <section id="section06">
      <div className="inner">
        <div className="desc">
          <span></span>
          <p className="title">WHAT MAKES YOUR WORK MEANINGFUL?</p>
          <p className="subtitle">자율적인 환경에서 같은 곳을 향한다는 신뢰를 바탕으로 열정적으로 일합니다.</p>
        </div>
        <div className="form">
          <div className="top_form">
            <div className="input_wrap">
              <input type="text" id="name" placeholder="NAME*" />
              <input type="text" id="e-mail" placeholder="E-mail*" />
              <input type="text" id="job_objective" placeholder="Job Objective*" />
            </div>
            <div className="text_wrap">
              <textarea placeholder="Find Job*"></textarea>
            </div>
          </div>
          <div className="submit">
            <input type="submit" value="입사지원 확인" />
          </div>
        </div>
      </div>
    </section>
  )
}

export default Section6;