import React, { useEffect, useRef, useState } from 'react';
import { Swiper, SwiperSlide, useSwiper } from 'swiper/react';
import SwiperCore from 'swiper';
import 'swiper/css';

const Section2: React.FC = () => {

  let winw: number = 0;
  const [swiperInstance, setSwiperInstance] = useState<SwiperCore | null>(null);
  const [isSwiperActive, setIsSwiperActive] = useState(false);

  const updateSwiperStatus = () => {
    const width = window.innerWidth;
    if (width < 720) {
      setIsSwiperActive(true);
    } else {
      setIsSwiperActive(false);
      if (swiperInstance) {
        swiperInstance.destroy(false, true);
        setSwiperInstance(null);
      }
    }
  };

  useEffect(() => {
    updateSwiperStatus();
    window.addEventListener('resize', updateSwiperStatus);
    return () => {
      window.removeEventListener('resize', updateSwiperStatus);
      if (swiperInstance) {
        swiperInstance.destroy(false, true);
      }
    };
  }, [swiperInstance]);

  return (
    <section id="section02">
      <div className="inner">
        <div className="desc">
          <span></span>
          <p className="title">HYBE LABELS</p>
          <p className="subtitle">각 레이블은 독립적이고 독창적인 크리에이티브 활동을 펼칩니다.</p>
        </div>
        {isSwiperActive ? (
          <Swiper
            onSwiper={setSwiperInstance}
            slidesPerView={'auto'}
            initialSlide={0}
            observer={true}
            observeParents={true}
            loop={true}
            className="sec02_swiper"
          >
            <SwiperSlide><a href=""><span>빅히트 뮤직</span><p>B</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>플레디스<br />엔터테인먼트</span> <p>P</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>빌리프랩</span> <p>B</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>하이브 레이블즈<br /> 재팬</span> <p>J</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>KOZ<br />엔터테인먼트</span><p>K</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>쏘스뮤직</span><p>S</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>빅 머신<br />레이블 그룹</span> <p>B</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>QC 뮤직</span><p>Q</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>어도어</span><p>A</p></a></SwiperSlide>
            <SwiperSlide><a href=""><span>네이코</span><p>N</p></a></SwiperSlide>
          </Swiper>
        ) : (
          <div className="sec02_swiper">
            <div className="swiper-wrapper">
              <div className="swiper-slide" data-aos="flip-right"><a href=""> <span>빅히트 뮤직</span><p>B</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="50"><a href=""> <span>플레디스<br />엔터테인먼트</span><p>P</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="100"><a href=""> <span>빌리프랩</span><p>B</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="150"><a href=""> <span>하이브 레이블즈<br /> 재팬</span><p>J</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="200"><a href=""> <span>KOZ<br />엔터테인먼트</span><p>K</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="250"><a href=""> <span>쏘스뮤직</span><p>S</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="300"><a href=""><span>빅 머신<br />레이블 그룹</span><p>B</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="350"><a href=""> <span>QC 뮤직</span><p>Q</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="400"><a href=""> <span>어도어</span><p>A</p></a></div>
              <div className="swiper-slide" data-aos="flip-right" data-aos-delay="450"><a href=""> <span>네이코</span><p>N</p></a></div>
            </div>
          </div>
        )}
      </div>
    </section>
  );
};



export default Section2;